<template>
  <div class="singleBlogItem">
    <headerItem></headerItem>
    <div class="content">
      <el-row :gutter="20">
        <el-col :span="10" :offset="6">
          <div style="border-bottom:1px solid #ddd">
            <div style="font-size:15px">
              <strong>{{psnBaseInfo.name}}</strong>
            </div>
            <div class="base-info-item status">{{showNowStatus}}</div>
            <div
              class="base-info-item"
            >基本信息：{{psnBaseInfo.age}} / {{psnBaseInfo.sex}} / {{psnBaseInfo.location}}/{{psnBaseInfo.workExp}}/{{psnBaseInfo.degree}}</div>
            <div class="base-info-item">
              求职意向：{{psnBaseInfo.jobName}} / {{psnBaseInfo.jobPay}} /
              {{psnBaseInfo.jobType}} / {{psnBaseInfo.jobAdd}}
            </div>
            <!-- <div class="base-info-item">手机：{{psnBaseInfo.tel}}</div>
            <div class="base-info-item">邮箱：{{psnBaseInfo.email}}</div>-->
            <p>
              <el-tag :key="index" v-for="(tag,index) in dynamicTags" type="success">{{tag}}</el-tag>
            </p>
          </div>
          <div>
            <div style="font-size:15px">
              <strong>项目经历</strong>
            </div>
            <div v-for="item in psnProjectInfo" :key="item.pk" class="for-info">
              <div class="for-item">
                <p>{{item.fields.startTime}}/{{item.fields.endTime}}</p>
                <div class="for-info-item" style="color:#67c23a">{{item.fields.orgName}}</div>
              </div>
              <div class="for-item">
                <p>{{item.fields.projectName}}</p>
                <div class="for-info-item">{{item.fields.jobName}}</div>
                <div class="for-info-item">相关技术：{{item.fields.tecName}}</div>
                <div class="for-info-item">项目描述：{{item.fields.projectDisp}}</div>
              </div>
            </div>
          </div>
          <div>
            <div style="font-size:15px">
              <strong>工作经历</strong>
            </div>
            <div v-for="item in psnWorkInfo" :key="item.pk" class="for-info">
              <div class="for-item">
                <p>{{item.fields.startTime}}/{{item.fields.endTime}}</p>
                <div class="for-info-item" style="color:#67c23a">{{item.fields.companyName}}</div>
              </div>
              <div class="for-item">
                <p>{{item.fields.jobName}}</p>
                <div class="for-info-item">工作描述：{{item.fields.workDisp}}</div>
              </div>
            </div>
          </div>
          <div>
            <div style="font-size:15px">
              <strong>教育经历</strong>
            </div>
            <div v-for="item in psnEduInfo" :key="item.pk" class="for-info">
              <div class="for-item">
                <p>{{item.fields.schoolName}}({{item.fields.degree}})</p>
              </div>
              <div class="for-item">
                <div class="for-info-item" style="color:#67c23a">{{item.fields.majorName}}</div>
                <div class="for-info-item">{{item.fields.gradTime}}届毕业生</div>
              </div>
            </div>
          </div>
          <div>
            <div style="font-size:15px">
              <strong>自我描述</strong>
            </div>

            <article style="font-size:14px">{{psnBaseInfo.selfDisp}}</article>
          </div>
        </el-col>
      </el-row>
    </div>

    <div class="comment">
      <el-row :gutter="20">
        <el-col :span="8" :offset="16">
          <el-button type="success" @click="dialogVisible=true" v-if="!jobid">邀请面试</el-button>
          <el-button type="success" @click="funPass(jobid)" v-else>邀请面试</el-button>
        </el-col>
      </el-row>
    </div>
    <footerItem></footerItem>
    <el-dialog title="选择" :visible.sync="dialogVisible" width="50%">
      <div v-if="entJobInfo.length==0">
        <p>
          暂无数据，请先
          <router-link to="addjob">发布职位</router-link>
        </p>
      </div>
      <el-table v-else :data="tempList" stripe style="width: 100%" size="small">
        <el-table-column prop="jobName" label="职位" width="120"></el-table-column>
        <el-table-column prop="jobPay" label="薪资"></el-table-column>
        <el-table-column prop="degree" label="学历"></el-table-column>
        <el-table-column prop="workExp" label="经验"></el-table-column>
        <el-table-column prop="updateTime" label="日期" sortable width="120"></el-table-column>
        <el-table-column label="操作" width="100">
          <template slot-scope="scope">
            <el-button @click="funPass(scope.row.jobid)" type="text" size="small">邀请</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        layout="prev, pager, next"
        :total="entJobInfo.length"
        style="text-align:center;"
        class="page"
        @size-change="funSizeChange"
        @current-change="funCurrentChange"
        :current-page="currentPage"
        :page-size="pageSize"
      ></el-pagination>
    </el-dialog>
  </div>
</template>

<script>
import Cookies from "vue-cookies";
import headerItem from "./childPageHeader";
import footerItem from "./childPageFooter";
export default {
  components: {
    headerItem,
    footerItem
  },
  name: "singleBlogItem",
  data() {
    return {
      dialogVisible: false,
      resumeid: "",
      psnid: "",
      jobid: "",
      showNowStatus: "",
      psnBaseInfo: {},
      dynamicTags: [],
      psnProjectInfo: [],
      psnWorkInfo: [],
      psnEduInfo: [],

      currentPage: 1,
      pageSize: 10,
      tempList: [],
      entJobInfo: []
    };
  },
  created() {
    this.resumeid = this.$route.query.resumeid;
    this.psnid = this.$route.query.psnid;
    this.jobid = this.$route.query.jobid;
    this.axios
      .post("searchPsn", {
        key: this.resumeid,
        type: "single"
      })
      .then(data => {
        if (data.data.status == "ok") {
          this.psnBaseInfo = JSON.parse(data.data.msg.psnBaseInfo)[0].fields;
          this.dynamicTags = this.psnBaseInfo.psnTag.split(",");
          switch (this.psnBaseInfo.nowStatus) {
            case "0":
              this.showNowStatus = "待沟通";
              break;
            case "1":
              this.showNowStatus = "学生";
              break;
            case "2":
              this.showNowStatus = "观望机会";
              break;
            case "3":
              this.showNowStatus = "求职中";
              break;
            case "4":
              this.showNowStatus = "暂不考虑";
              break;
          }
          this.psnProjectInfo = JSON.parse(data.data.msg.psnProjectInfo);
          this.psnWorkInfo = JSON.parse(data.data.msg.psnWorkInfo);
          this.psnEduInfo = JSON.parse(data.data.msg.psnEduInfo);
        }
      });
    this.axios
      .post("getEntBaseInfo", {
        entid: Cookies.get("entid"),
        key: "0000"
      })
      .then(data => {
        if (data.data.status == "ok") {
          let arr = JSON.parse(data.data.data.entJobInfo);
          for (var i in arr) {
            arr[i].fields.jobid = arr[i].pk;
            this.entJobInfo.push(arr[i].fields);
            this.tempList = this.entJobInfo.slice(0, 10);
          }
        }
      });
  },
  methods: {
    funSendResume() {
      this.axios
        .post("sendResume", {
          type: "entsend",
          entid: Cookies.get("entid"),
          psnid: this.psnid
        })
        .then(data => {
          if (data.data.status == "ok") {
            this.$message({
              type: "success",
              message: data.data.msg
            });
          } else {
            this.$message({
              type: "error",
              message: data.data.msg
            });
          }
        });
    },
    funPass(id) {
      this.axios
        .post("passSent", {
          msg: {
            jobid: id,
            resumeid: this.resumeid,
            entid: Cookies.get("entid"),
            psnid: this.psnid
          }
        })
        .then(data => {
          if (data.data.status == "ok") {
            this.$message({
              type: "success",
              message: data.data.msg
            });
            this.dialogVisible = false;
          }
        });
    },

    funSizeChange(pageSize) {
      this.pageSize = pageSize;
      this.funCurrentChange(this.currentPage);
    },
    funCurrentChange(currentPage) {
      this.currentPage = currentPage;
      this.currentChangePage(this.entJobInfo, currentPage);
    },
    //分页方法
    currentChangePage(list, currentPage) {
      let from = (currentPage - 1) * this.pageSize;
      let to = currentPage * this.pageSize;
      this.tempList = [];
      for (; from < to; from++) {
        if (list[from]) {
          this.tempList.push(list[from]);
        }
      }
    }
  }
};
</script>
<style scoped>
.content {
  padding: 60px;
  color: #303133;
}
.content-left {
  padding-bottom: 20px;
}
.content-left .title {
  font-size: 18px;
  color: #606266;
}
.content-left p {
  font-size: 14px;
  color: #909399;
}
.content-right .box-card {
  background: #f8f8f9;
}
article {
  padding: 10px;
}
.comment {
  padding: 60px;
  height: 36px;
  color: #303133;
  font-size: 18px;
  line-height: 36px;
}

.el-tag {
  margin-right: 10px;
}
.job-req {
  margin: 20px 5px;
  padding: 20px 5px;
}

.content-left p .job-pay {
  color: #f56c6c;
}
.content-left p .job-item {
  margin-right: 5px;
}
.base-info-item {
  color: #909399;
  font-size: 12px;
  margin-bottom: 10px;
}
.status {
  margin-top: 10px;
  color: #67c23a;
}
.for-info {
  border-bottom: 1px solid #ddd;
  display: flex;
}
.for-item {
  width: 33%;
  font-size: 12px;
  margin: 10px;
  margin-right: 80px;
}
.for-info-item {
  font-size: 12px;
  margin: 10px 0;
}
</style>
